/* eslint-disable */
// 401
import {checkAuthorization} from "./request";

const resp401 = {
  /**
   * 
   * @param response 
   * @param options  : {router, i18n, store, message}
   * @returns {*}
   */
  onFulfilled(response, options) {
    const {message} = options
    if (response.code === 401) {
      message.error('')
    }
    return response
  },
  /**
   * 
   * @param error 
   * @param options  : {router, i18n, store, message}
   * @returns {Promise<never>}
   */
  onRejected(error, options) {
    const {message} = options
    const {response} = error
    if (response.status === 401) {
      message.error('')
    }
    return Promise.reject(error)
  }
}

const resp403 = {
  onFulfilled(response, options) {
    const {message} = options
    if (response.code === 403) {
      message.error('')
    }
    return response
  },
  onRejected(error, options) {
    const {message} = options
    const {response} = error
    if (response.status === 403) {
      message.error('')
    }
    return Promise.reject(error)
  }
}

const reqCommon = {
  /**
   * 
   * @param config axios config
   * @param options  : {router, i18n, store, message}
   * @returns {*}
   */
  onFulfilled(config, options) {
    const {message} = options
    const {url, xsrfCookieName} = config

    if (url.indexOf('login') === -1 && xsrfCookieName && !checkAuthorization()) {
      // message.error(' token ')
    }
    let authorization = window.localStorage.getItem(xsrfCookieName);
    if (authorization){
      config.headers["token"] = authorization;
    }
    return config
  },
  /**
   * 
   * @param error 
   * @param options  : {router, i18n, store, message}
   * @returns {Promise<never>}
   */
  onRejected(error, options) {
    const {message} = options
    message.error(error.message)
    return Promise.reject(error)
  }
}

export default {
  request: [reqCommon], // 
  response: [resp401, resp403] // 
}



