/* eslint-disable */
import {USER} from './api'
import {METHOD, request} from '../utils/request'


/**
 *  
 * @param params {Object}
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function getEmailCode(params) {
  return request(USER.BINDING + '?email=' + params , METHOD.GET, params)
}


/**
 *  
 * @param params {Object}
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function bindingEmail(params) {
  return request(USER.BINDING, METHOD.PUT, params)
}


/**
 *  
 * @param params {Object}
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function changePass(params) {
  return request(USER.CHANGE, METHOD.PUT, params)
}


/**
 *  
 * @param params {Object}
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function login(params) {
  return request(USER.LOGIN, METHOD.POST, params)
}


/**
 *  
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function logout() {
  return request(USER.LOGOUT, METHOD.GET)
}


/**
 *  
 * @param params {Object}
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function register(params) {
  return request(USER.REGISTER, METHOD.POST, params)
}


/**
 *  
 * @param params {Object}
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function resetPass(params) {
  return request(USER.RESET, METHOD.PUT, params)
}


/**
 *  
 * @param params {Object}
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function resetCode(params) {
  return request(USER.RESET_CODE, METHOD.GET, params)
}



/**
 *  
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function userInfo() {
  return request(USER.USER_INFO, METHOD.GET)
}



